import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Prehistory from '../views/Prehistory.vue'
import Antiquity from '../views/Antiquity.vue'
import Medieval from '../views/Medieval.vue'
import Modern from '../views/Modern.vue'
import Postmodern from '../views/Postmodern.vue'
Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
    {
    path: '/prehistory',
    name: 'Prehistory',
    component: Prehistory
  },
  {
    path: '/antiquity',
    name: 'Antiquity',
    component: Antiquity
  },
  {
    path: '/medieval',
    name: 'Medieval',
    component: Medieval
  },
  {
    path: '/modern',
    name: 'Modernity',
    component: Modern
  },
  {
    path: '/postmodern',
    name: 'Postmodernity',
    component: Postmodern
  },

  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
